<template>
  <v-app class="body">
    <v-toolbar fixed app :clipped-left="clipped"  dark color="rgb(119, 153, 153)"><!--rgb(119, 153, 153)-->
      <img src="static/img/favi.png" alt="" >
      <v-toolbar-title v-text="title"></v-toolbar-title>
      <v-spacer></v-spacer>
      <v-toolbar-items  class="pctoolbar">
      <v-btn flat class="buclass" @click="toPage('/home')">Home</v-btn>
      <v-btn flat class="buclass" @click="toPage('/clp')">Call for Papers</v-btn>
      <v-btn flat class="buclass" @click="toPage('/imdate')">Important Dates</v-btn>
      <v-btn flat class="buclass" @click="toPage('/commitees')">Committees</v-btn>
      <v-btn flat class="buclass" @click="toPage('/venue')">Conference Venue</v-btn>
    </v-toolbar-items>

      <v-btn icon @click.stop="rightDrawer = !rightDrawer"  class="motoolbar">
        <v-icon>menu</v-icon>
      </v-btn>
    </v-toolbar>
    <v-content>
      <router-view></router-view>
    </v-content>
    <v-navigation-drawer temporary :right="right" v-model="rightDrawer" fixed>
      <v-list>
        <v-list-tile @click="toPage('/home')">
          <v-list-tile-action>
            <v-icon>home</v-icon>
          </v-list-tile-action>
          <v-list-tile>home</v-list-tile>
        </v-list-tile>
      </v-list>
      <v-list>
        <v-list-tile @click="toPage('/clp')">
          <v-list-tile-action>
            <v-icon>description</v-icon>
          </v-list-tile-action>
          <v-list-tile>Call for Papers</v-list-tile>
        </v-list-tile>
      </v-list>
      <v-list>
        <v-list-tile @click="toPage('/imdate')">
          <v-list-tile-action>
            <v-icon>calendar_today</v-icon>
          </v-list-tile-action>
          <v-list-tile>Important Dates</v-list-tile>
        </v-list-tile>
      </v-list>
      <v-list>
        <v-list-tile @click="toPage('/commitees')">
          <v-list-tile-action>
            <v-icon>face</v-icon>
          </v-list-tile-action>
          <v-list-tile>Committees</v-list-tile>
        </v-list-tile>
      </v-list>
      <v-list>
        <v-list-tile @click="toPage('/venue')">
          <v-list-tile-action>
            <v-icon>where_to_vote</v-icon>
          </v-list-tile-action>
          <v-list-tile>Conference Venue</v-list-tile>
        </v-list-tile>
      </v-list>
    </v-navigation-drawer>
    <v-footer  dark height="auto">
      <v-card  flat tile dark class="footflex" >
        <v-card-title class="cardcolor" >
          <table class="table">
            <tr>
              <td>
                <strong class="sponser">Sponsered By</strong>
              </td>
              <td class="td">
                <a href="http://sysu.edu.cn/" target="_blank">
                  <img src="static/img/logo2.jpg" alt="中山大学" class="spimg">
                </a></td>
              <td class="td"><a href="https://www.xidian.edu.cn/" target="_blank">
                  <img src="static/img/xidian.gif" alt="西安电子科技大学" class="spimg">
                </a></td>
              <td class="td">
                <img src="static/img/LOFN.jpg" alt="Lab of NSC" class="spimg">
              </td>
              <td class="td">
                <img src="static/img/IACR.jpg" alt="IACR" class="spimg">
              </td>
            </tr>
          </table>
          <v-divider dark></v-divider>
        </v-card-title>
        <div class="cpright"><strong>The Fifth International Workshop on Cyber Security</strong> (CS-2019)
          <br>Copyright &copy;2019 CS-2019. All rights reserved.
        </div>
      </v-card>
    </v-footer>
  </v-app>
</template>

<script>
  export default {
    data () {
      return {
        clipped: true,
        drawer: false,
        fixed: false,
        routerclass: true,
        miniVariant: false,
        right: true,
        rightDrawer: false,
        title: 'The Fifth International Workshop on Cyber Security',
        banner_text: [{
          blue: true
        }]
      }
    },
    methods: {
      toPage (uri) {
        this.$router.push(uri)
      }
    }
  }
</script>

<style>
  .body {
    font-family: "Montserrat", Helvetica, sans-serif;
  }

  .spimg {
    width: 60%;
  }

  .sponser {
    text-align: center;
    font-size: 20px;
    word-spacing: 1px;
    clear: both;
  }

  .intro {
    width: 30%;
  }

  .photo {
    float: right;
    text-align: center;
    margin: 0 auto;
    width: 70%;
  }

  .cardcolor {
    background-color: rgb(94, 139, 139);
    width: 100%;
    text-align: center;
    margin: 0 auto;
  }

  .table {
    margin: 0 auto;
  }

  .td {
    margin: 0 auto;
    padding: 1rem;
    height: fit-content;
    width: 20%;
  }

  .cpright {
    text-align: center;
    background-color: rgb(94, 139, 139);
  }

  .footflex {
    width: 100%;
    text-align: center;
  }
  .buclass{
    text-transform: initial; 
    font-size: 15px;
  }
  .motoolbar{
    display: none;
  }
  .pctoolbar{
    display: inline-block;
  }
  @media (max-width: 960px) {
    .motoolbar{
      display: inline-block;
    }
    .pctoolbar{
      display: none;
    }
  }
</style>



