import Vue from 'vue'
import Router from 'vue-router'
import Home from '../components/home'
import Callforpaper from '../components/clp'
import imdate from '../components/imdate'
import comm from '../components/commitees'
import ven from '../components/venue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/home',
      name: 'Home',
      component: Home
    },
    {
      path: '/',
      name: 'hp',
      component: Home
    },
    {
      path: '/clp',
      name: 'Call For Paper',
      component: Callforpaper
    },
    {
      path: '/imdate',
      name: 'important dates',
      component: imdate
    },
    {
      path: '/commitees',
      name: 'commitees',
      component: comm
    },
    {
      path: '/venue',
      name: 'venue',
      component: ven
    }
  ]
})



