<template>
  <v-container fluid class="homebg">
      <v-layout row wrap>
        <v-flex xs10 offset-xs1>
          <v-carousel>
            <v-carousel-item v-for="(item,i) in items" :key="i" :src="item.src"></v-carousel-item>
            <div class="filter"></div>
            <v-content class="intro-box">
              <h1 class="intro-box.head">The Fifth International Workshop on Cyber
                Security</h1>
              <h3 class="intro-box.sub">(CS-2019)November 16-18, 2019, Guang-zhou, China</h3>
            </v-content>
          </v-carousel>
        </v-flex>
      </v-layout>
    <v-layout column wrap class="my-5" align-center>
      <v-flex xs12 sm4 class="my-3">
        <div>
          <h2 class="headline text-xs-center">The CS-2019 will be held in Guang-zhou, China on November 16-18, 2019</h2>
          <p class="subtitle text-xs-center">The Fifth International Workshop on Cyber Security</p>
        </div>
        <div class="cardclass">
          With the rapid development of cyber technology,the application of network is becoming more and more widely
          and deeply,
          the means of cyber attack become more easier, precision and stronger.The situation of network and
          information security is becoming increasingly rigorous,
          all problem become more prominent.Therefore, the cyber security and information has become a major
          strategic issues of
          national security and national development. While the attack systems have become more
          easy-to-use, sophisticated, and powerful, interest has greatly increased in the field
          of building more effective, intelligent, adaptive, active and high performance defense
          systems which are distributed and cybered.
        </div>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
  export default {
    data () {
      return {
        items: [{
          src: 'static/img/gate.jpg'
        },
        {
          src: 'static/img/canton-tower.jpg'
        },
        {
          src: 'static/img/sysumotto.jpg'
        },
        {
          src: 'static/img/canton.jpg'
        }
        ]
      }
    }
  }
</script>

<style>
  .intro-box {
    color: white;
    text-shadow: black 1px 1px 2px;
    clear: both;
    font-size: large;
    text-align: center;
    font-family: "Montserrat", Helvetica, sans-serif;
  }

  .intro-box.head {
    text-transform: uppercase;
  }

  .intro-box.sub {
    font-size: x-small;
    text-transform: lowercase;
  }

  .subtitle {
    font-size: 20px;
    align-self: center;
    font-family: "Montserrat", Helvetica, sans-serif;
  }

  .cardclass {
    border: none;
    padding-left: 8.4rem;
    padding-right: 8.4rem;
    text-align: justify;
    word-spacing: 1px;
    word-break: break-all;
    font-family: "Montserrat", Helvetica, sans-serif;
  }

  .homebg {
    background-color: rgb(239, 239, 240);
  }

  .filter {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(7, 17, 27, 0.4);
  }
  .header {
    background-color: rgb(239, 239, 240);
    font-weight: 100;
    text-align: left;
    padding-right: 2rem;
    padding-bottom: 2rem;
    border-width: 1px;
    height: 100%;
    width: 100%;
  }

  .content {
    font-weight: 100;
    text-align: left;
    padding-right: 2rem;
    padding-bottom: 2rem;
    border-width: 1px;
    height: 100%;
    width: 100%;
  }
</style>



